import React from 'react';
import { withRouter } from 'react-router';
import { connect } from "react-redux";
import { Row, Col, Table, Tabs, message, Dropdown, Menu,Icon} from 'antd';
// import { pathConstants } from 'constants/path-constants';
import './index.scss';
import * as Sockets from '../../sockets/socket';
// Actions
import { default as LogsActions } from "store/logs/actions";
// components
import Loader from 'components/loader';
// import { moment } from 'moment';
// constants
const APP_CONSTANTS = require('constants/app-constants')
const TabPane = Tabs.TabPane;
// const Option = Select.Option;

const { getParamLogsData, getGraphLogsData } = LogsActions;

class Logs extends React.Component {
  constructor(props) {
    super(props);
   
    this.state = {
        paramLogs:[],
        dGraphLogs:[],
        selectedGraphLogs:[],
        dgraphAlphaLogs:[],
        dgraphZeroLogs:[],
        loadingForlogs:false,
        activeTab:"1",
        dropdownText:"",
        
        
    }
    this.fetchParamLogSocketData = this.fetchParamLogSocketData.bind(this);
    this.tabSwitch = this.tabSwitch.bind(this);
  }

  componentDidMount(){
    //   var connection;
    //   var self = this;
      this.setState({loadingForlogs:true});
      this.props.getParamLogsData();
      this.props.getGraphLogsData();
      this.getSocketData(); 
       
  }

  componentWillReceiveProps(nextProps){
      console.log('nextprop'+nextProps.paramlogsData);
      console.log('nextprop'+nextProps.dGraphlogsData);
    //   console.log('nextprop'+JSON.stringify(nextProps));
      if(nextProps && nextProps.paramlogsData && nextProps.paramlogsData !== this.props.paramlogsData){
          if(nextProps.paramlogsData.type !== "GET_PARAM_LOGS_ERROR_RESULT"){
            this.getParamTableData(nextProps.paramlogsData.dailyRotateFile);
            
        }else{
             this.setState({loadingForlogs:false});
            message.error("Param logs API Failed");
        }
      }

      if(nextProps && nextProps.dGraphlogsData && nextProps.dGraphlogsData !== this.props.dGraphlogsData){
        if(nextProps.dGraphlogsData.type !== "GET_DGRAPH_LOGS_ERROR_RESULT"){
            this.getdGraphTableData(nextProps.dGraphlogsData);
          
      }else{
           this.setState({loadingForlogs:false});
           message.error("dGraph logs API Failed");
      }
    }

  }

  getSocketData = () => {
    var connection;
    var self = this;
    debugger;
    if(Sockets.socketConnection !== undefined){
            connection = Sockets.socketConnection;
    }else{
            connection = Sockets.connectSocket();
    }
    // connection.on("paramLogs", function (data) {
    //     console.log("SOCKET DATA RECEIVED for param logs" + JSON.stringify(data))
    //     self.fetchParamLogSocketData(data);
    // });
    connection.on("alpha", function (data) {
        debugger;
        console.log("SOCKET DATA RECEIVED for logs aplha" + JSON.stringify(data))
        self.fetchAlphaLogSocketData(data);
    });
    connection.on("zero", function (data) {
        console.log("SOCKET DATA RECEIVED for logs zero" + JSON.stringify(data))
        self.fetchZeroLogSocketData(data);
    });
    
  }


  tabSwitch(key){
    var currentTab ;
    if(key === "tab1"){
        currentTab = "1";
    }else if(key === "tab2"){
        currentTab = "2";
    }else{
        currentTab = "3";
    }
    this.setState({activeTab:currentTab});
  }


  getParamTableData(data) {
      const paramTableData = [];
        if(data.length>0){
            for(var i in data){
                data[i].key =  data[i].timestamp;
            }
            this.setState({ paramLogs: (data),loadingForlogs:false}, function () {
                // console.log(this.state.paramLogs);
            });
        }else{
            this.setState({ paramLogs: (paramTableData),loadingForlogs:false}, function () {
                // console.log(this.state.paramLogs);
            });
        }
     
  }

  fetchParamLogSocketData = (data) => {
      var existingData = this.state.paramLogs;
        existingData.splice(0,0,data.res);
        this.setState({ paramLogs: (existingData),loadingForlogs:false}, function () {
                // console.log(this.state.paramLogs);
        });
  }

  fetchAlphaLogSocketData = (data) => {
    var existingData = this.state.dgraphAlphaLogs;
    for(var i in data.content){
        existingData.splice(i,i,data.content[i]);
    }
    // existingData.splice(0,0,data.content);
    this.setState({ dgraphAlphaLogs: (existingData),loadingForlogs:false}, function () {
            console.log(this.state.dgraphAlphaLogs);
    });
  }

  fetchZeroLogSocketData = (data) => {
    var existingData = this.state.dgraphZeroLogs;
    for(var i in data.content){
        existingData.splice(i,i,data.content[i]);
    }
    this.setState({ dgraphZeroLogs: (existingData),loadingForlogs:false}, function () {
            console.log(this.state.dgraphZeroLogs);
    });
  }

  getdGraphTableData(data){
    for(var i in data.alpha){
      data.alpha[i].key = i;
    }
      this.setState({ dGraphLogs: data,
        loadingForlogs:false,
        dropdownText:"alpha",
        dgraphAlphaLogs:data.alpha,
        selectedGraphLogs:data.alpha,
        }, function () {
            //   console.log(this.state.dGraphLogs);
            //   console.log(this.state.selectedGraphLogs);
      });
  }

  

  responseMenuSelected = (e) => {
    // console.log('record'+e.key);
    var keys = Object.keys(this.state.dGraphLogs)
    if(e.key === "0"){
        for(var i in this.state.dGraphLogs.alpha){
            this.state.dGraphLogs.alpha[i].key =  i;
        }
        this.setState({ loadingForlogs:false,
            selectedGraphLogs:this.state.dGraphLogs.alpha,
            dropdownText:keys[e.key],
            }, function () {
                //   console.log(this.state.selectedGraphLogs);
          });
    }else{
        for(var k in this.state.dGraphLogs.zero){
            this.state.dGraphLogs.zero[k].key =  k;
        }
        this.setState({ loadingForlogs:false,
            dropdownText:keys[e.key],
            dgraphZeroLogs:this.state.dGraphLogs.zero,
            selectedGraphLogs:this.state.dGraphLogs.zero,
            }, function () {
                //   console.log(this.state.selectedGraphLogs);
          });
    } 
}


    render() {
        const dropdownMenu = [];
        var keys = Object.keys(this.state.dGraphLogs)
            for(var i in keys){
                    dropdownMenu.push(
                            <Menu.Item key={i}>
                                <a>
                                {
                                    this.state.dGraphLogs?
                                        keys[i]
                                    :null
                                }</a>
                            </Menu.Item>
                    )
            }    
        const menu = (
            <Menu  onClick={this.responseMenuSelected}>{dropdownMenu}</Menu>
        )
      
      return (
        <div className="logs">
            <Row>
                <Tabs defaultActiveKey={this.state.activeTab} onChange={this.tabSwitch}>
                        <TabPane tab="Param Network" key="1">
                        {
                           !this.state.loadingForlogs?
                                this.state.paramLogs?
                                    <div>
                                        <Table columns={APP_CONSTANTS.ParamLogsColumns}
                                            dataSource={this.state.paramLogs}
                                            pagination={true}                                           
                                        />
                                    </div>
                                :null
                          :<Loader />
                        }
                        </TabPane>
                        {/* <TabPane tab="Composer" key="2">
                           
                        </TabPane> */}
                        <TabPane tab="dGraph" key="2">
                            <div>
                                <Row>
                                    <Col md={6} xs={24} >
                                    {this.state.dropdownText?
                                        <Dropdown   overlay={menu} 
                                                    trigger={['click']}
                                                    
                                                    >
                                                <h3 className="ant-dropdown-link logs__dropdown">
                                                    {this.state.dropdownText} <Icon type="down" />
                                                </h3>
                                        </Dropdown>
                                        :null
                                    }
                                    </Col>
                                    <Col md={6} xs={24} ></Col>
                                </Row><br />
                              
                              {
                                  this.state.dropdownText === "alpha"?
                                    <Table columns={APP_CONSTANTS.dgraphlogsColumns} 
                                            dataSource={this.state.dgraphAlphaLogs}
                                    />
                                  :
                                    <Table columns={APP_CONSTANTS.dgraphlogsColumns} 
                                            dataSource={this.state.dgraphZeroLogs}
                                    />

                              }
                                {/* <Table columns={APP_CONSTANTS.dgraphlogsColumns} 
                                        dataSource={this.state.selectedGraphLogs}
                                /> */}
                            </div>
                        </TabPane>
                </Tabs>
            </Row>
        </div>
      );
    }

}
function mapStateToProps(state) {
    return {
        ...state,
        paramlogsData: state.logs.paramlogs,
        dGraphlogsData: state.logs.dGraphlogs,
    }
}
export default connect(
    mapStateToProps,
    {
        getParamLogsData,
        getGraphLogsData,
    })(withRouter(Logs));


// export default withRouter(Logs);
